<template>
  <div class="topbar-box">
    <div class="topbar">
      <slot></slot>
    </div>
  </div>
</template>

<script>
var that= {};
export default {
  name: 'topbar',

  data () {
    return {

    }
  },
  props: {

   },
   beforeCreate(){
        that = this;
    },
  methods:{
    hideModal:function(){
      this.ishideModal = true
    },
    /*回首页*/
    backHome(){
      this.$router.push({path: '/home'});
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.topbar-box{
  width:100%;
  height:80px;
  overflow:hidden;
  position:fixed;
  top:0;
  z-index:120;
  display:flex;
  align-items: center;
  justify-content: center;
  background:#2F54EB;
  .topbar{
    display:flex;
    flex-direction: row;
    i{
      margin-right:14px;
      color:#fff;
      font-size:30px;
    }
    span{
      font-size:30px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(255,255,255,1);
    }
  }
}
</style>



